@charset "UTF-8";
/* todo (default)  */
.z-btn.v-btn:not(.v-btn--round) {
  text-transform: initial;
}
.z-btn.v-btn:not(.v-btn--round).v-size--x-small {
  height: 20px;
}
.z-btn.v-btn:not(.v-btn--round).v-size--x-small > .v-btn__content {
  transform: scale(0.9);
}
.z-btn.v-btn:not(.v-btn--round).v-size--x-small .v-btn__loader .v-progress-circular {
  max-width: 12px;
  max-height: 12px;
}
.z-btn.v-btn:not(.v-btn--round).v-size--small {
  height: 28px;
}
.z-btn.v-btn:not(.v-btn--round).v-size--small .v-btn__loader .v-progress-circular {
  max-width: 16px;
  max-height: 16px;
}
.z-btn.v-btn:not(.v-btn--round).v-size--default {
  height: 32px;
}
.z-btn.v-btn:not(.v-btn--round).v-size--default .v-btn__loader .v-progress-circular {
  max-width: 20px;
  max-height: 20px;
}
.z-btn.v-btn:not(.v-btn--round).v-size--large {
  height: 38px;
}
.z-btn.v-btn:not(.v-btn--round).v-size--large .v-btn__loader .v-progress-circular {
  max-width: 20px;
  max-height: 20px;
}
.z-btn.v-btn:not(.v-btn--round).v-size--x-large {
  height: 44px;
}
.z-btn.v-btn:not(.v-btn--round).v-size--x-large .v-btn__loader .v-progress-circular {
  max-width: 20px;
  max-height: 20px;
}

.v-btn--fab.v-size--x-small {
  width: 20px;
  height: 20px;
}
.v-btn--fab.v-size--small {
  width: 28px;
  height: 28px;
}
.v-btn--fab.v-size--default {
  width: 32px;
  height: 32px;
}
.v-btn--fab.v-size--large {
  width: 38px;
  height: 38px;
}
.v-btn--fab.v-size--x-large {
  width: 44px;
  height: 44px;
}

.v-btn--icon.v-size--x-small {
  width: 20px;
  height: 20px;
}
.v-btn--icon.v-size--small {
  width: 28px;
  height: 28px;
}
.v-btn--icon.v-size--default {
  width: 32px;
  height: 32px;
}
.v-btn--icon.v-size--large {
  width: 38px;
  height: 38px;
}
.v-btn--icon.v-size--x-large {
  width: 44px;
  height: 44px;
}

.z-btn.v-btn.secondary--text:not(.v-btn--text) {
  border: solid 1px rgba(0, 0, 0, 0.2);
}